.navbar {
  position: fixed;
  width: 100%;
  background-color: #fff;
  z-index: 3;
  box-shadow: 0px 2px 10px rgb(0 0 0 / 10%);
  overflow: hidden;
}
.navbar-toggler:focus {
  text-decoration: none;
  outline: 0;
  box-shadow: none !important;
}
.logo {
  font-weight: 600;
  font-family: "Poppins", sans-serif;
}
.logo span {
  color: #e03a3c !important;
}
.menu a {
  color: #000 !important;
  font-weight: 500;
}
.menu .active {
  color: #e03a3c !important;
}
.menu a:hover {
  color: #e03a3c !important;
}
.nav-btn button {
  background-color: #e03a3c;
  color: #fff;
  font-weight: 600;
  border: none;
}
.nav-btn button:hover {
  background-color: #000;
  color: #fff;
}

.nav {
  padding-left: 0 !important;
}

.nav-logo img{
	width: 30px;
	height: 30px;
	border-radius: 50%;
	margin-top: 7px;
}

/* Progress Bar */
.scroll-progress {
	position: fixed;
	top: 0;
	left: 0;
	height: 5px;
	background: linear-gradient(90deg, #f7f4f4, #ec580e);
	z-index: 1000;
	transition: width 0.2s ease-out;
  }
  
.banner {
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.6),
      rgba(0, 0, 0, 0.1)
    ),
    url("../src/assets/subash-bg.jpg");
  background-position: center;
  background-size: cover;
}
.banner h1 {
  font-size: 48px;
  color: #fff;
  font-family: "Poppins", sans-serif;
}
.banner h2 {
  font-size: 22px;
  font-weight: 600;
  color: #fff;
  font-family: "Raleway", sans-serif;
}
.clients-img {
  min-height: 8vh;
}
.clients img {
  width: 100%;
  filter: grayscale(100%);
  opacity: 0.8;
}
.clients img:hover {
  filter: grayscale(0%);
}

.carousel-inner {
  position: relative;
  width: 100%;
}
.circle .carousel-indicators button {
  display: inline-block;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  color: #e03a3c;
  border: solid 1px #e03a3c;
  background-color: #fff;
  margin: 0 3px;
  cursor: pointer;
  transform: translateY(50px);
}
.circle .carousel-indicators button:hover {
  background-color: #e03a3c;
}
.circle .carousel-indicators button.active {
  background-color: #e03a3c;
}
.about {
  position: relative;
}
.about::after {
  content: "";
  top: 0;
  left: 0;
  position: absolute;
  transform: skewY(-3deg);
  height: 100%;
  width: 100%;
  background-color: #1b1b1b;
  z-index: -1;
}
.abo h2 {
  color: #fff;
}
.abo p {
  color: #fff;
}

.button a {
  text-decoration: none;
  color: #fff;
  border-radius: 25px;
  border: solid 2px #e03a3c;
  font-weight: 600;
}
.button a:hover {
  background-color: #e03a3c;
  color: #fff;
}

.inner-box h4 {
  color: #fff;
}
.inner-box p {
  color: #848484;
  font-size: 14px;
  line-height: 28px;
}
.inner-box iconify-icon {
  color: #e03a3c;
}

.abt-cont {
  border: none !important;
}
.abt-cont .nav-item {
  width: 50%;
}
.abt-cont .nav-item iconify-icon {
  line-height: 30px;
  padding: 10px 10px;
  font-size: 40px;
}
.abt-cont .nav-item .nav-link {
  border: solid 1px #ccc !important;
  margin: 0 10px;
  color: #000;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.abt-cont .nav-item:last-child .nav-link {
  margin-right: 0;
}
.abt-cont .nav-item .active {
  background-color: #e03a3c !important;
  color: #fff !important;
  border: solid 1px #e03a3c;
}
.abt-cont .nav-item .nav-link:hover {
  color: #e03a3c;
}
.abt-content img {
  width: 100%;
}
.abt-content .first {
  font-style: italic !important;
}
.abt-content li {
  list-style-type: none;
  line-height: 26px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 4px 0px;
}
.abt-content iconify-icon {
  font-size: 20px;
  padding: 0px 8px;
  color: #e03a3c;
}
/* end------------------------------ */
.services {
  position: relative;
}
.header-sec {
  text-align: center;
}
.header-sec h1 {
  color: #fff;
  position: relative;
  padding: 20px 0px;
}
.header-sec h1::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 60px;
  height: 3px;
  background-color: #e03a3c;
  transform: translateX(-50%);
}
.header-one {
  text-align: center;
}
.header-one h1 {
  color: #1b1b1b;
  position: relative;
  padding: 20px 0px;
}
.header-one h1::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 60px;
  height: 3px;
  background-color: #e03a3c;
  transform: translateX(-50%);
}
.header-sec p {
  color: #fff;
  padding: 10px 0px;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
}
.services::after {
  content: "";
  top: 0;
  left: 0;
  position: absolute;
  transform: skewY(-2deg);
  height: 100%;
  width: 100%;
  background-color: #000;
  z-index: -1;
}
.ser {
  display: flex;
  justify-content: center;
  background-color: #252525;
  padding: 20px 0px;
}
.ser-icon {
  font-size: 40px;
  color: #e03a3c;
  padding: 0px 30px;
}
.ser-cont a {
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  font-size: 18px;
}
.ser-cont a:hover {
  color: #fff;
  border-bottom: solid 1px #fff;
  transition: 0.5s;
}
.ser-cont p {
  color: #fff;
  font-size: 16px;
}

.carousel-item {
  padding: 10px;
}

.contact-1 {
  padding: 0px 10px;
}
.contact-2 h1 {
  margin: 10px 10px;
  font-size: 20px;
  font-family: "Poppins", sans-serif;
  font-weight: 800;
  color: #656262;
}
.contact-2 p {
  padding: 0px 0px;
  line-height: 22px;
}
.contact span iconify-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: dotted 1px #e03a3c;
  background-color: white;
  color: #e03a3c;
  text-align: center;
  line-height: 70px;
  font-size: 30px;
  flex: 0 0 auto;
  padding: 14px;
}
.cont {
  background-color: #fff;
  text-align: center;
  margin: 20px 0px;
  padding: 20px 20px;
  box-shadow: 0 0 30px rgb(214 215 216 / 60%);
  border-radius: 5px;
}
.footer {
  background-color: #1b1b1b;
  padding: 30px 0px;
}
.footer-la-1 {
  padding: 30px 0px;
}
.footer-la-1 h1 {
  color: #fff;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  font-size: 30px;
  padding-bottom: 10px;
  letter-spacing: 1px;
}
.footer-la-1 b {
  color: #e03a3c !important;
}
.footer-la-1 p {
  padding-bottom: 30px;
  color: #fff;
}
.footer-la-1 span {
  font-weight: 600;
  color: #fff;
  font-size: 18px;
  padding: 0px 4px;
}
.footer-la-1 h4 {
  color: #fff;
  font-size: 14px;
  font-family: "Raleway", sans-serif;
  padding: 4px 0px;
}
.footer-la h3 {
  font-weight: bold;
  font-size: 18px;
  font-family: "Raleway", sans-serif;
  color: #fff;
  position: relative;
}
.footer-la h3::after {
  content: "";
  position: absolute;
  margin-top: 38px;
  left: 0;
  width: 22px;
  height: 3px;
  background-color: #e03a3c;
}
.footer-la ul li a {
  padding: 0px 0px;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #aaaaaa;
  transition: 0.3s;
}
.footer-la ul {
  padding: 20px 0px;
}
.footer-la ul li {
  padding: 8px 0px;
  display: flex;
  align-items: center;
  color: #656262;
  list-style: none;
}
.footer-la ul li a:hover {
  color: #fff;
}
.footer-la {
  padding: 30px 0px;
}
.footer-la p {
  padding: 28px 0px;
  font-family: "Poppins", sans-serif;
  color: #fff;
}
.footer-end {
  background-color: #000;
  padding: 20px 0px;
}
.footer-mail {
  padding: 10px 0px;
}

.footer-mail p {
  color: #fff;
}
.footer-mail p a:hover {
  color: #e76668;
}
.footer-mail p a {
  text-decoration: none;
  color: #e03a3c;
}

.footer-la li iconify-icon {
  color: #fff;
}
.footer-logo a {
  background-color: #1b1b1b;
  padding: 8px 10px;
  border-radius: 5px;
  color: #fff;
  text-align: center;
  margin: 0px 0px 0px 4px;
}
.footer-logo a:hover {
  color: #fff;
  background-color: #e03a3c;
  transition: 0.5s;
}
